import React, { useState } from "react";
import { useForm } from "react-hook-form";

interface Props {
  submitButton?: string;
  showMessage?: boolean;
}

const Form = ({
  submitButton = " ",
  showMessage = false,
}: Props) => {
  const [show, setShow] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();
  const validateOptions = {
    name: { required: "   " },
    phone: {
      required: "   ",
      pattern: {
        value: /^[0-9+-]+$/,
        message: "     ",
      },
      minLength: {
        value: 9,
        message: "      9 ",
      },
    },
  };
  const onSubmit = (data: any) => {
    setShow(true);
    reset();
  };

  return (
    <>
      {!show && (
        <form onSubmit={handleSubmit(onSubmit)}>
          <>
            <input
              type="text"
              placeholder=""
              className={errors.name ? "wrong-field" : ""}
              {...register("name", validateOptions.name)}
            />
            {errors.name && (
              <p className="error">
                <>{errors.name?.message}</>
              </p>
            )}
            <input
              type="text"
              placeholder=" "
              className={errors.phone ? "wrong-field" : ""}
              {...register("phone", validateOptions.phone)}
            />
            {errors.phone && (
              <p className="error">
                <>{errors.phone?.message}</>
              </p>
            )}
            {showMessage && (
              <textarea
                placeholder=""
                {...register("message")}
              ></textarea>
            )}
            <input type="submit" value={submitButton} />
          </>
        </form>
      )}
      {show && (
        <div className="mail-status">   !</div>
      )}
    </>
  );
};

export default Form;
